* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -webkit-tap-highlight-color: transparent;
}
html,
body {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  font-family: "Kirang Haerang", cursive;
}
#canvas {
  width: 100%;
  height: 50%;
}
#textBox {
  width: 100%;
  height: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #a9b6cc;
}
.sentence {
  font-size: 100px;
  font-weight: bold;
  margin: 0.5em;
  line-height: 200%;
}
.color1 {
  color: #95998a;
}
.color2 {
  color: #bdcc94;
}
.color3 {
  color: #ffec67;
}
.color4 {
  color: #c3b2af;
}
.color5 {
  color: #a9b6cc;
}
